<template lang="pug">
f7-page#coaches
  navbar-bridge-swift(
    v-if='!isWeb && !isInAppWeb',
    title='先生一覧',
    back,
    @click='onClickBack'
  )

  .navigaion-bar(v-if='isInAppWeb')
    svg-icon(icon='header_logo')
    f7-link(href='#', @click='didTapLogin') ログイン

  popup-filter-dance-kinds(
    :opened='opened',
    @click='setDanceKindsCondition',
    @close='opened = false'
  )

  popup-sort-coaches(
    :opened='openSort',
    @setSortCondition='setSortCondition',
    @close='openSort = false'
  )

  coach-swiper(@didTapLessonFlow='didTapLessonFlow')

  coaches-top(
    :isFetching='isFetching',
    :hasNextPage='hasNextPage',
    @clickLogin='didTapLogin',
    @clickFilter='opened = true',
    @clickSort='openSort = true',
    @clickLessonFlow='didTapLessonFlow',
    @movePage='movePage',
    @setKeywordCondition='setKeywordCondition'
  )

  coach-list#scroll-top(
    :coaches='coaches',
    @click='showDetail',
    @reviewClick='showReview'
  )

  coach-bottom-button(
    v-if='isInAppWeb && showCoachBottomButton',
    @goToSignUp='goToSignUp'
  )
</template>

<style lang="scss" scoped>
.pagination {
  margin-bottom: 46px;
}

.navigaion-bar {
  display: flex;
  align-items: center;
  margin-top: 16px;
  .icon {
    width: 86px;
    height: 38px;
    margin-left: 16px;
  }
  a {
    margin-right: 24px;
    margin-left: auto;
    @extend %login-link-text;
  }
}
</style>

<script>
import { mapActions, mapGetters } from 'vuex'
import BridgeSwift from '@/components/mixins/BridgeSwift'
import CoachesTop from '@/components/organisms/CoachesTop'
import CoachList from '@/components/organisms/CoachList'
import Pagination from '@/components/organisms/Pagination'
import NavbarBridgeSwift from '@/components/organisms/NavbarBridgeSwift'
import coaches from '@/components/queries/coaches'
import Pager from '@/components/mixins/Pager'
import CoachSwiper from '@/components/organisms/CoachSwiper'
import CoachBottomButton from '@/components/organisms/CoachBottomButton'
import PopupFilterDanceKinds from '@/components/organisms/PopupFilterDanceKinds'
import PopupSortCoaches from '@/components/organisms/PopupSortCoaches'

export default {
  components: {
    CoachesTop,
    CoachList,
    Pagination,
    NavbarBridgeSwift,
    CoachSwiper,
    CoachBottomButton,
    PopupFilterDanceKinds,
    PopupSortCoaches,
  },
  mixins: [Pager, BridgeSwift],
  data() {
    return {
      coaches: [],
      opened: false,
      openSort: false,
      danceKinds: [],
      sortBy: 'REVIEW_RATE_AVERAGE',
      isFetching: false,
      keyword: null,
      acceptNewStudent: false,
      showCoachBottomButton: false,
    }
  },
  computed: {
    ...mapGetters('displayType', [
      'isWeb',
      'isInAppWeb',
      'isSignedInAndAppDisplayType',
    ]),
  },
  methods: {
    ...mapActions('displayType', ['setDisplayType']),
    showDetail(coachId) {
      this.$f7router.navigate({
        name: 'PublicCoachDetail',
        params: { id: coachId },
      })
    },
    showReview(coachId) {
      this.$f7router.navigate({
        name: 'PublicCoachReviews',
        params: { id: coachId },
      })
    },
    inAppDisplayType() {
      this.setDisplayType('no_logo')
    },
    signedInAndAppDisplayType() {
      this.setDisplayType('coach_list_only')
    },
    goToSignUp() {
      this.postMessageToSwift('didTapIntroduceTeacher')
      this.$f7router.navigate({
        name: 'PublicSignUp',
      })
    },
    didTapLogin() {
      this.postMessageToSwift('didTapLogin')
    },
    didTapLessonFlow() {
      this.postMessageToSwift('didTapShowLessonFlow')
    },
    onClickBack() {
      this.postMessageToSwift('didTapBack')
    },
    // Set keyword condition for filter coaches
    setKeywordCondition(keyword) {
      this.keyword = keyword
      this.search()
    },
    // Set dance kinds and accept new student condition for filter coaches
    setDanceKindsCondition(danceKinds, acceptNewStudent = null) {
      this.danceKinds = danceKinds || []
      this.acceptNewStudent = acceptNewStudent
      this.opened = false
      this.search()
    },
    // Set sort condition for coaches
    setSortCondition(sortBy) {
      this.sortBy = sortBy
      this.search()
    },
    // Handle search and sort coaches by conditions
    search() {
      // if not check acceptNewStudent, send param which be null
      this.acceptNewStudent = this.acceptNewStudent ? true : null

      this.optionParams = {
        keyword: this.keyword,
        acceptNewStudent: this.acceptNewStudent,
        danceKinds: this.danceKinds,
        sort: { sortBy: this.sortBy, order: 'DESCENDING' },
      }

      this.movePage('front')
    },
    // Handle scroll event to show/hide fixed bottom button
    handleScrollPage() {
      document.addEventListener(
        'scroll',
        (ev) => {
          if (ev.target.scrollTop > 0) {
            this.showCoachBottomButton = true
            document.getElementById(
              'coaches'
            ).firstElementChild.style.paddingBottom = '125px'
          } else {
            this.showCoachBottomButton = false
            document.getElementById(
              'coaches'
            ).firstElementChild.style.paddingBottom = '0'
          }
        },
        true
      )
    },
  },
  created() {
    this.countPerPage = 30
    this.params.first = this.countPerPage

    this.inAppDisplayType()
  },
  mounted() {
    this.postMessageToSwift('didShowCoaches')

    // Handle scroll event when user not login
    if (this.isInAppWeb) {
      this.handleScrollPage()
    }
  },
  apollo: {
    coaches: {
      ...coaches,
      variables() {
        return { ...this.params }
      },
      watchLoading(isLoading) {
        this.isFetching = isLoading
      },
    },
  },
}
</script>
