<template lang="pug">
f7-page#coach-detail
  transparent-navbar(v-if="scrollPosition < 404")
  navbar(v-else :title="coachName" back)

  //- bottom tool bar
  .apply-contract(v-if="isInAppWeb" class="toolbar toolbar-bottom-md")
    .toolbar-inner
      large-multiline-button(
        title="この先生の無料体験レッスンを希望する"
        class="apply-contract-button"
        :disabled="!isAcceptableNewStudent"
        @click="showSignUp")
      p.text-warning(v-if="!isAcceptableNewStudent") 定員に達したため、新規受け入れをストップしています
      p(v-else) 先生の空き状況により、ご希望に添えない場合があります。

  //- page content
  coach-query(:coachId="coachId" @onResult="onResult")
    div(slot-scope="{ coach }")
      coach-detail-top(:coach="coach")

      coach-detail-body(:coach="coach" class="coach-detail-body" @click="showReview")
</template>

<style lang="scss" scoped>
.toolbar {
  position: fixed;
}
.apply-contract {
  height: 100px !important;
  background: #101c2a;

  .toolbar-inner {
    flex-direction: column;
    justify-content: space-evenly;
    padding: 8px 24px;

    .text-warning {
      color: $color-notification;
    }
  }

  .apply-contract-button {
    width: 100%;
  }

  p {
    @extend %description;
    margin: 0;
  }
}

.coach-detail-body {
  padding-bottom: 92px !important;
  position: absolute;
  top: 460px;
  width: 100%;
}
</style>

<script>
import { mapGetters } from 'vuex'
import BridgeSwift from '@/components/mixins/BridgeSwift'
import TransparentNavbar from '@/components/organisms/TransparentNavbar'
import CoachDetailTop from '@/components/organisms/CoachDetailTop'
import CoachDetailBody from '@/components/organisms/CoachDetailBody'
import LargeMultilineButton from '@/components/atoms/LargeMultilineButton'
import CoachQuery from '@/components/queries/CoachQuery'

export default {
  components: {
    TransparentNavbar,
    CoachDetailTop,
    CoachDetailBody,
    LargeMultilineButton,
    CoachQuery
  },
  mixins: [BridgeSwift],
  data() {
    return {
      coachId: null,
      coachName: null,
      appLoggedIn: false,
      isAcceptableNewStudent: false,
      scrollPosition: 0
    }
  },
  computed: {
    ...mapGetters('displayType', ['isInAppWeb'])
  },
  methods: {
    onResult(coach) {
      this.coachName = coach.name
      this.isAcceptableNewStudent = coach.isAcceptableNewStudent
    },
    showSignUp() {
      this.postMessageToSwift('didTapIntroduceTeacherWithCoachId')
      this.$f7router.navigate({
        name: 'PublicSignUp',
        params: { coachId: this.coachId },
      })
    },
    showReview(reviewId) {
      this.$f7router.navigate({
        name: 'PublicCoachReviews',
        params: { id: this.coachId, reviewId: reviewId },
      })
    },
    contentScrolled(position) {
      this.scrollPosition = position
    },
  },
  created() {
    this.coachId = this.$f7route.params.id
  },
  mounted() {
    this.postMessageToSwift('didShowCoachDetail', this.coachId)
    this.$f7.$('.page-content').on('scroll', (e) => {
      this.contentScrolled(e.srcElement.scrollTop)
    })
  },
}
</script>
