<template lang="pug">
f7-popup(:opened='opened')
  f7-page
    popup-navigation-bar(title='並びかえ', @close='$emit("close")')
    background
      .sort-selections
        label.custom-radio(v-for='condition in conditions')
          | {{ condition.label }}
          input(
            type='radio',
            v-model='selectedSort',
            name='radio',
            :value='condition.value',
            @change='onChange'
          )
          span.checkmark
</template>

<script>
import LargeButton from '@/components/atoms/LargeButton'
export default {
  components: { LargeButton },
  props: {
    opened: { type: Boolean },
  },
  data() {
    return {
      selectedSort: 'REVIEW_RATE_AVERAGE',
      conditions: [
        { value: 'REVIEW_RATE_AVERAGE', label: 'レビューの評価が高い順' },
        { value: 'REVIEW_COUNT', label: 'レビューの件数が多い順' },
      ],
    }
  },
  methods: {
    onChange() {
      this.$emit('setSortCondition', this.selectedSort)
      this.$emit('close')
    },
  },
}
</script>

