<template lang="pug">
.coaches-top(ref='coachTopWrapper')
  template(v-if='isInAppWeb || isSignedInAndAppDisplayType')
    .info
      input#search-input(v-model='keyword', @change='handleChangeInput')

      .filter-and-flow
        narrow-genre-button.btn-filter(
          title='ジャンルを選択',
          @click='$emit("clickFilter")'
        )
        sort-review-button.btn-sort(title='並びかえ', @click='$emit("clickSort")')

  template(v-else)
    retina-image(:name='name', style='width: 100%')
    .title(:style='{ "margin-bottom": marginBottom }')
      p レッスンを受けたい先生を選んで、
        br/
        | 申し込みをしよう！
      p ※申し込みは先着順の為、レッスン枠が埋まっている場合があります。
</template>

<style lang="scss">
.coaches-top {
  width: 100%;
  position: relative;
  z-index: 2;

  .info {
    margin-bottom: 10px;
    padding: 0 16px;

    p {
      margin: 0;
      text-align: center;
      margin: 24px 0 16px 0;
    }

    .skip-sign-up {
      text-align: center;
      margin: 24px 0;
    }

    .filter-and-flow {
      position: relative;
      padding: 0 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 10px 0;
      padding: 0;

      .button {
        height: 40px;
        display: flex;
        align-items: center;
        background: transparent;

        .button-box {
          @extend %narrow-button;
          @extend %narrow-button-text;
          border: none;
          box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
          color: $text-primary-color;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: auto;
          font-size: 14px;

          .svg-icon {
            display: inline-flex;
          }
        }
      }

      .btn-filter {
        margin: 0 !important;
        padding: 0;
      }

      .btn-sort {
        .button-box {
          border-left: 2px solid rgba(255, 255, 255, 0.12);
          border-radius: unset;
          padding-left: 10px;
        }
      }

      .icon-lesson-flow {
        position: absolute;
        right: 10px;
      }
    }

    @media (min-width: 768px) {
      .filter-and-flow {
        justify-content: space-around;
      }
    }

    .largeButton {
      margin: 0 8px !important;
      @media (max-width: 375px) {
        font-size: 15px !important;
      }
      @media (max-width: 320px) {
        font-size: 13px !important;
      }
    }

    &.is-flex {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .title {
    position: absolute;
    bottom: 0;
    margin-bottom: 42px;
    padding: 0 16px;

    p {
      margin: 0;
    }

    :first-child {
      @extend %headline-detail-text;
      margin-bottom: 8px;
    }
  }

  #search-input {
    position: relative;
    width: 100%;
    height: 40px;
    border-radius: 6px;
    border: none;
    color: white;
    text-align: center;
    font-size: 16px;
    background-color: rgb(55, 65, 77);
    background-image: url('~@/assets/search-placeholder.png');
    background-repeat: no-repeat;
    background-position: center;

    &:focus {
      background-image: none;
