</style>

<script>
import { mapGetters } from 'vuex'
import LargeButton from '@/components/atoms/LargeButton'
import NarrowGenreButton from '@/components/atoms/NarrowGenreButton'
import SortReviewButton from '@/components/atoms/SortReviewButton'
import Icon from '@/components/atoms/Icon'

const displayTypes = {
  no_logo: { name: 'app_coaches_top', marginBottom: '24px' },
  coach_list_only: {},
  with_logo: { name: 'web_coaches_top', marginBottom: '42px' },
}

export default {
  props: {
    isFetching: { type: Boolean, default: false },
    hasNextPage: { type: Boolean, default: false },
  },
  components: {
    LargeButton,
    NarrowGenreButton,
    SortReviewButton,
    Icon,
  },
  data() {
    return {
      keyword: null,
    }
  },
  computed: {
    ...mapGetters('displayType', [
      'isInAppWeb',
      'isSignedInAndAppDisplayType',
      'displayType',
    ]),
    name() {
      return displayTypes[this.displayType].name
    },
    marginBottom() {
      return displayTypes[this.displayType].marginBottom
    },
  },
  mounted() {
    document
      .querySelector('#coaches .page-content')
      .addEventListener('scroll', this.handlePageScrollDown)
  },
  beforeDestroy() {
    const coachesDOM = document.querySelector('#coaches .page-content')
    if (coachesDOM) {
      coachesDOM.removeEventListener('scroll', this.handlePageScrollDown)
    }
  },
  methods: {
    handlePageScrollDown(e) {
      this.fetchMoreCoach(e)
    },

    fetchMoreCoach(e) {
      if (
        e.target.scrollHeight - e.target.clientHeight <=
        e.target.scrollTop + 300
      ) {
        if (!this.isFetching && this.hasNextPage) {
          this.$emit('movePage', 'next')
        }
      }
    },

    // handle change input event
    handleChangeInput() {
      this.keyword = this.keyword.trim()
      this.$emit('setKeywordCondition', this.keyword)
    },
  },
}
</script>
