<template lang="pug">
swiper(ref='swiperRef', :options='swiperOptions', @click.native='slideClicked')
  swiper-slide
    #video
      video.topVideo(
        width='100%',
        :poster='require(`@/assets/bnr-intro-movie.png`)',
        controls=''
      )
        source(
          src='https://www.w3schools.com/tags/movie.mp4',
          type='video/mp4'
        )
        source(
          src='https://www.w3schools.com/tags/movie.ogg',
          type='video/ogg'
        )
        |
        | Your browser does not support the video tag.
  swiper-slide
    #lesson-flow(@click='$emit("didTapLessonFlow")')
      img(:src='require(`@/assets/bnr-lesson-flow.png`)')
  .swiper-pagination(slot='pagination')
</template>

<script>
export default {
  data() {
    return {
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination',
        },
        autoplay: {
          delay: 2000,
          disableOnInteraction: false,
        },
        loop: true,
      },
    }
  },
  computed: {
    swiper() {
      return this.$refs.swiperRef.swiper
    },
  },
  methods: {
    setTopVideo() {
      let media = document.getElementsByClassName('topVideo')[0]

      if (media.requestFullscreen) {
        media.requestFullscreen()
      } else if (media.mozRequestFullScreen) {
        /* Firefox */
        media.mozRequestFullScreen()
      } else if (media.webkitRequestFullscreen) {
        /* Chrome, Safari & Opera */
        media.webkitRequestFullscreen()
      } else if (media.msRequestFullscreen) {
        /* IE/Edge */
        media.msRequestFullscreen()
      }
    },
    slideClicked(event) {
      if (event.target.classList.contains('topVideo')) {
        this.setTopVideo()
      }
    },
  },
}
</script>

<style lang="scss">
#video {
  width: 100%;
  height: auto;

  video {
    height: calc((100vw - 32px) * 130 / 343);
  }
}

#lesson-flow {
  width: 100%;
  height: auto;
  text-align: center;

  img {
    width: 100%;
  }
}

.swiper-container {
  margin: 16px 16px 12px;

  .swiper-wrapper {
    margin-bottom: 28px;
  }

  .swiper-slide {
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
  }

  .swiper-pagination {
    bottom: -2px;

    .swiper-pagination-bullet {
      background: rgba(255, 255, 255, 0.6);

      &-active {
        background: rgba(255, 255, 255, 0.9);
      }
    }
  }
}
</style>
