import { FETCH_COACHES_QUERY } from '@/graphql'
import createCoach from '@/entities/Coach'
import Vue from 'vue'

export default {
  query: FETCH_COACHES_QUERY,
  update(data) {
    Vue.store.commit('pageInfo/SET', {
      ...data?.publicCoaches?.pageInfo,
      totalCount: data?.publicCoaches?.totalCount,
      totalPageCount: data?.publicCoaches?.totalPageCount
    })

    const publicCoaches = data?.publicCoaches?.edges || []

    let coaches = publicCoaches.map(e => createCoach(e.node))

    return this.params.after ? this.coaches.concat(coaches) : coaches
  }
}
